import { TabItem } from '@/types/photos';
import './PhotoTabs.css';

export default function PhotoTabs() {
  const tabs: TabItem[] = [
    { id: 'tab-1', label: 'Photos of you', active: true },
    { id: 'tab-2', label: 'Your photos' },
    { id: 'tab-3', label: 'Recently added' },
    { id: 'tab-4', label: 'Family' },
    { id: 'tab-5', label: 'Albums' },
  ];

  return (
    <ul className="nav nav-tabs nav-bottom-line justify-content-center justify-content-md-start">
      {tabs.map((tab) => (
        <li key={tab.id} className="nav-item">
          <a 
            className={`nav-link ${tab.active ? 'active' : ''}`} 
            data-bs-toggle="tab" 
            href={`#${tab.id}`}
          >
            {tab.label}
          </a>
        </li>
      ))}
    </ul>
  );
}