import Image from 'next/image';
import { Photo } from '@/types/photos';
import './PhotoCard.css';

interface PhotoCardProps {
  photo: Photo;
}

export default function PhotoCard({ photo }: PhotoCardProps) {
  return (
    <div className="position-relative">
      <div className="position-absolute bottom-0 end-0">
        <div className="dropdown mb-2 me-3">
          <button 
            className="icon-sm bg-primary text-white rounded-circle" 
            id={`photoActionEdit${photo.id}`}
            data-bs-toggle="dropdown"
          >
            <i className="bi bi-pencil-fill"></i>
          </button>
          
          <ul className="dropdown-menu dropdown-menu-end" aria-labelledby={`photoActionEdit${photo.id}`}>
            <li><a className="dropdown-item" href="#"><i className="bi bi-tag fa-fw pe-1"></i> Remove Tag</a></li>
            <li><a className="dropdown-item" href="#"><i className="bi bi-download fa-fw pe-1"></i> Download</a></li>
            {/* Más opciones... */}
          </ul>
        </div>
      </div>
      
      <a href={photo.imageUrl} data-gallery="image-popup" data-glightbox={`description: .custom-desc${photo.id}; descPosition: left`}>
        <Image 
          src={photo.imageUrl} 
          alt={photo.alt} 
          width={300}
          height={200}
          className="rounded img-fluid"
        />
      </a>
      
      {/* Contenido de glightbox */}
      <div className={`glightbox-desc custom-desc${photo.id}`}>
        {/* Detalles de la foto */}
      </div>
    </div>
  );
}