import AlbumGrid from '../../components/albums/AlbumGrid';
import PhotoTabs from '../../components/albums/PhotoTabs';
import Button from '../../components/ui/Buttom';
import Card from '../../components/ui/Card';
import styles from './page.module.css'; // Importación del CSS Module

export default function PhotosPage() {
  return (
    <div className={styles.container}>
      <div className={`${styles.row} ${styles.g4}`}>
        {/* Sidebar - Omitido por brevedad (similar al anterior) */}
        
        {/* Main content */}
        <div className={`${styles.colMd8} ${styles.colLg6} ${styles.vstack} ${styles.gap4}`}>
          <Card className={styles.card}>
            <div className={`${styles.cardHeader} ${styles.dSmFlex} ${styles.textCenter} ${styles.alignItemsCenter} ${styles.justifyContentBetween} ${styles.border0} ${styles.pb0}`}>
              <h1 className={`${styles.cardTitle} ${styles.h4}`}>Photos</h1>
              <Button variant="primary" icon="plus" dataBsToggle="modal" dataBsTarget="#modalCreateAlbum">
                Create album
              </Button>
            </div>
            
            <div className={styles.cardBody}>
              <PhotoTabs />
              <AlbumGrid />
            </div>
          </Card>
        </div>
      </div>
      
      {/* Modal - Se implementará como componente aparte */}
    </div>
  );
}