import PhotoCard from './PhotoCard';
import { Photo } from '@/types/photos';
import './AlbumGrid.css';


export default function AlbumGrid() {
  const photos: Photo[] = [
    {
      id: 1,
      imageUrl: '/assets/images/albums/01.jpg',
      alt: 'Photo 1',
      likes: 56,
      comments: 12,
      author: 'Lori Ferguson',
      authorAvatar: '/assets/images/avatar/04.jpg',
      description: "I'm so privileged to be involved in the @bootstrap hiring process! #internship #inclusivebusiness"
    },
    // Más fotos...
  ];

  return (
    <div className="tab-content mb-0 pb-0">
      <div className="tab-pane fade show active" id="tab-1">
        <div className="row g-3">
          {/* Add photo card */}
          <div className="col-6 col-lg-3">
            <div className="border border-2 border-dashed h-100 rounded text-center d-flex align-items-center justify-content-center position-relative">
              <a className="stretched-link" href="#">
                <i className="fa-solid fa-camera-retro fs-1"></i>
                <h6 className="mt-2">Add photo</h6>
              </a>
            </div>
          </div>
          
          {/* Photo cards */}
          {photos.map(photo => (
            <div key={photo.id} className="col-6 col-lg-3">
              <PhotoCard photo={photo} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}